import React from "react";
import "./languages.scss";

const Languages = ({triggeredTranslate}: {triggeredTranslate: any}) => {
  return(
    <div id="languages" className="my-card">
      <label className="title font-primary">{triggeredTranslate.text_lang}</label>
      <ul>
        <li><i className="fa-solid fa-c"></i>C</li>
        <li><i className="icon-cpp"></i>C++</li>
        <li><span className="icon-c--4"><span className="path1"></span><span className="path2"></span><span className="path3"></span><span className="path4"></span><span className="path5"></span></span>C#</li>
        <li><i className="fa-brands fa-java"></i>Java</li>
        <li><i className="fa-brands fa-php"></i>PHP</li>
        <li><i className="fa-brands fa-python"></i>Python</li>
        <li><i className="fa-brands fa-html5"></i>HTML</li>
        <li><i className="fa-brands fa-css3-alt"></i>CSS/SCSS</li>
        <li><i className="fa-brands fa-js"></i>JavaScript</li>
        <li><span className="icon-typescript"><span className="path1"></span><span className="path2"></span></span>TypeScript</li>
        <li><i className="fa-solid fa-code"></i>XML</li>
        <li><i className="icon-dart"><i className="path1"></i></i>Dart</li>
        <li><i className="fa-solid fa-database"></i>SQL</li>
      </ul>
    </div>
  )
}

export default Languages;