import React from "react";
import "./languageSwitch.scss"

const LanguageSwitch = ({setLang, lang}: {setLang: React.Dispatch<React.SetStateAction<"it" | "en">>, lang: "it" | "en"}) => {
  return(
    <div className="lang">
      <input type="checkbox" className="tgl tgl-flip" id="lang" onChange={e => setLang(e.target.checked ? 'it': 'en')} checked={lang === "it"} />
      <label className="tgl-btn" htmlFor="lang"></label>
    </div>
  );
}

export default LanguageSwitch;